import React from 'react'
import BlockInterests from './interests'
import ProjectTypeIconContainer from '../icons/containers/projectType'
import BlockLevels from './levels'
import BlockUsers from './users'
import * as users from '../mocks/data/users'

const Feature = (props) => {
    

    return (
       <>
                
                <div className="header">
                    <div><ProjectTypeIconContainer type={2} small />
                        </div>
                    <div>The fruit fly: an amazing animal model
                        </div> 
                </div>
                <div className="content">
                    <div className="text">
                        How the fruitfly has shaped bioscience knowledge.
                    </div>
                    <div>
                    <BlockInterests interests={[2,8,11]} />
                    </div>
                    <div className="users">
                    By <BlockUsers users={[users.u24, users.u18, users.u14]} />
                    </div>
                    <div>
                    <BlockLevels levels={[{type:1, nb:4}, {type:2, nb:1}, {type:3, nb:2}, {type:4, nb:2}]} />
                    </div>
                    
                </div>
               
                
                <style jsx>
                {`  
                   .header {
                    font: Bold 14px/19px Arial;
                    letter-spacing: 0;
                    color: #000000;
                    
                    display: flex;
                    align-items: center;

                    margin-top: 20px;
                    padding-bottom: 20px;
                   }
                   
                   .header div {
                       margin-right: 5px;
                   }

                   .content {
                       width: 100%;
                       display:flex;
                       flex-direction:column;
                   }
                   .content div {
                       margin-bottom: 10px;
                   }
                   .text {
                    display: flex;
                    align-items: center;
                    justify-content:center;
                    font: 12px/17px Arial;
                    letter-spacing: 0;
                    color: #707070;
                   }

                   .users {
                    display: flex;
                    align-items: center;
                   }

                `}

                </style>

        </>
                

    )
}

export default Feature;